import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/user',
            name: 'user',
            component: () => import('@/views/user/user.vue'),
            meta: {title: '用户中心', requireAuth: true, login: false},
        },
        {
            path: '/home',
            name: 'home',
            component: () => import('@/views/home/home.vue'),
            meta: {title: '首页', requireAuth: true, login: false},
        },
        {
            path: '/payFees',
            name: 'payFees',
            component: () => import('@/views/home/pay-fees/pay-fees.vue'),
            meta: {title: '首页', requireAuth: true, login: false},
        },
        {
            path: '**',
            component: () => import('@/views/home/home.vue'),
            meta: {title: '首页', requireAuth: true, login: false},
        }
    ],
});
